@charset "utf-8";

/* 공통으로 사용되는 클래스 */

/* hidden  */
.hidden-elem {
  visibility: hidden;
  position: absolute;
  top: -10000px;
  height: 1px;
  width: 1px;
}

/*clear*/
.clear {
  float: left;
}
.clear:after {
  content: "";
  display: block;
  clear: both;
  width: 0;
  height: 0;
  overflow: hidden;
}

/*정렬클래스*/
.al {
  text-align: left !important;
}
.ar {
  text-align: right !important;
}
.ac {
  text-align: center !important;
}

/*폰트칼라*/
.fc-blue {
  color: #15acd3 !important;
}
.fc-orange {
  color: #ff9100 !important;
}
.fc-red {
  color: #e7305a !important;
}
.fc-gray {
  color: rgba(0, 0, 0, 0.6) !important;
}

/*폰트 style */
.fwb {
  font-weight: bold !important;
}
.fwn {
  font-weight: normal !important;
}

